@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400&family=Playfair+Display:wght@700&display=swap);
body {
  font-family: 'Montserrat', sans-serif;
}

h1, h2 {
  font-family: 'Playfair Display', serif;
  color: #003049;
}

.red-button {
  background: #d62828;
  margin: 4px 0;
  border: 0px;
}

.red-button:hover,
.red-button:active,
.red-button:focus {
  background: #003049;
  border: 0px;
}

.is-flex {
  display: flex;
}

.form-margin {
  margin-top: 16px;
}

.is-centered {
  align-items: center;
  justify-content: center;
}

.alert {
  border: 1px solid #d62828;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.brand {
  font-family: 'Playfair Display', serif;
  color: #003049;
  font-size: 1.5em;
}

a {
  text-decoration: none;
  color: #212529;
}

a:hover,
a:active,
a:focus {
  color: #d62828;
  text-decoration: underline;
}


h1 {
  text-align: center;
  margin: 16px 0px;
}

.salon-map {
  background: #003049;
  height: 250px;
  width: 300px;
  margin: 0px 0px 24px 0px;
}

.pro-cta {
  text-align: center;
  margin: 12px auto;
}
/* #d62828; */

article {
  border: 3px solid #003049;
  margin: 4px 0px;
  border-radius: 0.25rem;
}

h2 {
  font-size: 1.5em;
}

p {
  text-align: left;
  margin: 0px;
}

.service-container {
  margin: 12px 4px;
  align-items: center;
  justify-content: space-around;
}

.price {
  text-align: right;
}

.welcome {
  text-align: center;
}

h2 {
  text-align: center;
  margin-top: 16px;
}

.packCard, .date {
  display: flex;
  border: 3px solid #003049;
  margin: 4px 0px;
  align-items: center;
  justify-content: space-between;
  max-width: 360px;
  border-radius: 0.25rem;
}

.date{
  margin-right: 4px;
  padding: 4px 0px 2px 12px;
}

.packCard {
  padding: 8px 12px;
}

.packCard label {
  margin-bottom: 0px;
}

.packCard p {
  margin: 1px 0px;
}

.price {
  text-align: right;
  font-size: 1.3em;
}

.packs {
  align-items: center;
  justify-content: space-between;
}

.service-label {
  margin: 0px;
  text-align: left;
}

img {
  width: 100%;
}

.salon-info-tidbits {
  text-transform: capitalize;
}

.rating-module,
.salon-info-tidbits {
  font-size: 1.2em;
}

.rating-module {
  text-align: right;
}

.accordion-menu {
  margin-top: 12px;
}
